/*-----------------------
     Setting up
------------------------*/
body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 100%;
  line-height: 1.6;
  color: #3A3A3A;
  background: #f8f8f8;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/*-----------------------
    General styles
------------------------*/
body {
  font-size: 1rem; }

h4 {
  font-size: 1.414rem; }

h3 {
  font-size: 1.414rem; }

h2 {
  font-size: 1.9994rem; }

h1 {
  font-size: 2.82715rem; }

small, .font_small {
  font-size: 0.70721rem; }

ul {
  padding: 0 0 1.75em 1.25em; }

h1, h2, h3, h4 {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #333399;
  font-weight: bold; }

/*-----------------------
    Link Styles
------------------------*/
a,
a:visited,
a:focus,
a:active {
  color: #333399;
  text-decoration: none;
  outline: 0; }

a:hover {
  color: #0f0f2e;
  text-decoration: underline; }

/*----------------------
   Setup
-----------------------*/
/*----------------------
   Header wrapper
-----------------------*/
.header {
  background-color: #FFFFFF;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.22); }

/*----------------------
   Logo
-----------------------*/
.site-logo img {
  float: left;
  width: auto;
  height: 40px;
  margin: 10px 0; }

/*----------------------
   Mobile menu button
-----------------------*/
.menu-btn {
  float: right;
  margin-top: 12px; }
  @media only screen and (min-width: 768px) {
    .menu-btn {
      display: none !important; } }

/*-----------------------
     Mobile menu
-------------------------*/
.site-nav {
  max-height: 0;
  width: 100%;
  transition: max-height .2s ease-in-out;
  overflow: hidden;
  background: white; }
  .site-nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center; }
  .site-nav li > a {
    color: #aebdc1;
    font-weight: bold;
    display: block;
    padding: 0.75em;
    white-space: nowrap; }
    .site-nav li > a:hover {
      text-decoration: none;
      color: #333399; }
  .no-js .site-nav,
  .is-menu-on .site-nav {
    max-height: 300px; }

/*-----------------------------
   Navigation desktop layout
------------------------------*/
@media only screen and (min-width: 768px) {
  .site-nav {
    max-height: none;
    width: auto;
    float: right;
    overflow: visible; }
    .site-nav li {
      display: inline-block;
      text-align: left;
      position: relative; }
    .site-nav li a {
      padding: 0 1em;
      line-height: 60px; }
    .site-nav li.current-menu-item > a,
    .site-nav li.current_page_item > a,
    .site-nav li.current_page_ancestor > a,
    .site-nav .current-menu-ancestor > a {
      color: #333399; } }

a.site-logo img {
  width: 67%; }

/*----------------------
   Sub menu
-----------------------*/
@media only screen and (min-width: 768px) {
  .site-nav .sub-menu {
    position: absolute;
    z-index: 100;
    right: 0;
    padding: 0;
    background: #f5f5f5; }
  a.site-logo img {
    width: auto; } }

/*----------------------
   Sub menu
-----------------------*/
@media only screen and (min-width: 768px) {
  .site-nav .menu-item-has-children .sub-menu {
    visibility: hidden;
    opacity: 0;
    z-index: 100;
    transition: all 0.3s ease-in-out; }
  .site-nav .menu-item-has-children:hover .sub-menu {
    visibility: visible;
    opacity: 1; } }

/*----------------------
    pictures outline
 ----------------------*/
.lekker-galery {
  overflow: hidden; }
  .lekker-galery .image-block {
    position: relative;
    float: left;
    margin: 5px;
    height: 138px; }

@media only screen and (min-width: 768px) {
  .lekker-galery .image-block {
    height: 160px; } }

/*------------------------
 *  Image
 *-----------------------*/
.image-content {
  max-width: 500px;
  margin: 0 auto;
  padding: 0; }
  .image-content .attachment-block {
    position: relative;
    border-radius: 14px;
    width: 100% !important;
    height: 500px;
    max-height: 800px;
    margin: 0 auto !important;
    overflow: hidden; }
    .image-content .attachment-block.orientation-tall {
      max-width: 500px; }
    .image-content .attachment-block a img.wide {
      height: auto !important; }
    .image-content .attachment-block a img.tall {
      width: auto !important; }
    .image-content .attachment-block ​ #attachment-rating.rating-red,
    .image-content .attachment-block .attachment-controls {
      position: absolute;
      background-color: rgba(0, 0, 0, 0.6);
      text-align: center;
      color: #fff;
      font-weight: 700;
      width: 100%;
      font-size: 3em; }
    .image-content .attachment-block #attachment-rating.rating-red {
      bottom: 10%;
      font-size: 2em;
      z-index: +1; }
    .image-content .attachment-block #attachment-rating.rating-red span[class^="rating-star-"] {
      font-size: 0.5em; }
    .image-content .attachment-block #attachment-rating.rating-red span[class^="rating-star-"]:before {
      content: "\2605";
      /* position: absolute; */
      position: relative;
      font-size: 2em;
      left: 3%;
      color: red;
      z-index: -1; }
    .image-content .attachment-block .attachment-controls {
      bottom: 5px;
      unicode-bidi: bidi-override;
      direction: rtl; }
      .image-content .attachment-block .attachment-controls .attachment-previous a span,
      .image-content .attachment-block .attachment-controls .attachment-next a span {
        vertical-align: middle;
        margin-bottom: 0.4em;
        font-size: 1em; }
      .image-content .attachment-block .attachment-controls .attachment-previous {
        float: right;
        padding-right: 20px; }
      .image-content .attachment-block .attachment-controls .attachment-next {
        float: left;
        padding-left: 40px; }
      .image-content .attachment-block .attachment-controls a, .image-content .attachment-block .attachment-controls a:hover {
        color: #fff;
        font-weight: 700; }
  .image-content table.rating {
    width: 60%;
    margin: 0 auto;
    border: 0; }

.image-decription.l-col-6 {
  border: 1.5px solid #000;
  padding: 13px;
  height: 500px;
  border-radius: 14px;
  overflow: hidden; }
  .image-decription.l-col-6 h1#entry-title {
    font-size: 2em;
    margin-top: 0;
    margin-bottom: 0.3em; }
  .image-decription.l-col-6 div.entry-meta {
    overflow: auto; }
    .image-decription.l-col-6 div.entry-meta .l-col-6 {
      width: 48%;
      display: inline-block; }
    .image-decription.l-col-6 div.entry-meta ul.image-info-block, .image-decription.l-col-6 div.entry-meta img {
      padding: 0;
      margin: 0; }
  .image-decription.l-col-6 div.entry-description h2 {
    font-size: 1.4em;
    margin-top: 0;
    margin-bottom: 0.3em; }
  .image-decription.l-col-6 table.rating {
    border-spacing: 0;
    font-size: 0.7em; }
    .image-decription.l-col-6 table.rating th, .image-decription.l-col-6 table.rating td {
      text-align: left;
      font-size: 1.6em;
      width: 49%;
      padding: 0 0 0 14px; }
  .image-decription.l-col-6 ul.image-info-block {
    list-style: none; }

.attachment-block .attachment-controls div[class^="attachment-star"]:hover:before {
  content: "\2605";
  position: absolute; }

.attachment-controls > div[class^="attachment-star"]:hover:before,
.attachment-controls > div[class^="attachment-star"]:hover ~ div[class^="attachment-star"]:before {
  content: "\2605";
  position: absolute;
  color: gold; }

.attachment-block .attachment-controls div.rated-red:before {
  content: "\2605";
  position: absolute;
  color: red; }

.attachment-block .attachment-controls div[class^="attachment-"] {
  display: inline;
  margin: 0px; }

html, body.home {
  background-image: url(/wp-content/themes/lekker-kontje/images/beach-babe.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  color: #fff;
  height: 100%; }
  html header.entry-header.l-container, body.home header.entry-header.l-container {
    padding-top: 10%; }
  html h2.entry-title, body.home h2.entry-title {
    float: right; }
  html div#site-container, body.home div#site-container {
    height: 100%; }
  html a:active, html a:visited, html a, body.home a:active, body.home a:visited, body.home a {
    color: #ffffff;
    text-decoration: underline; }

@media only screen and (max-width: 768px) {
  html, body.home {
    background-position: 25% bottom;
    background-size: initial; } }
